import React from "react";
import Header from "@/components/tempcomponent/Header";

// ... import other section components

// Map section key â†’ component
const sectionComponents: Record<string, React.FC<any>> = {
  header: Header,

  // add more as you create them
};

interface Template1Props {
  data: {
    sections: { section: string; content: any }[];
    styles?: any;
  };
}

export default function Template1({ data }: Template1Props) {
  const { sections = [] } = data;

  return (
    <div className="min-h-screen bg-white">
      {sections.map(({ section, content }) => {
        const Component = sectionComponents[section];

        if (!Component) {
          console.warn(`No component found for section: ${section}`);
          return (
            <div key={section} className="p-8 text-center text-gray-400 border border-dashed">
              Missing component: {section}
            </div>
          );
        }

        return <Component key={section} content={content} />;
      })}
    </div>
  );
}